import React from 'react'
import "./css/Tokonomics.css";
// import Tokenomics2 from "../images/T1.jpg";
// import Tokenomics1 from "../images/Tokenomics-new1.jpg";
import AOS from 'aos';
import 'aos/dist/aos.css';
export default function Tokonomics() {
    
  setTimeout(() => {
    const $card = document.querySelector('.toko--cards');
    let bounds;

    function rotateToMouse(e) {
      const mouseX = e.clientX;
      const mouseY = e.clientY;
      const leftX = mouseX - bounds.x;
      const topY = mouseY - bounds.y;
      const center = {
        x: leftX - bounds.width / 2,
        y: topY - bounds.height / 2
      }
      const distance = Math.sqrt(center.x **4+ center.y **4);
      
      $card.style.transform = `
      scale3d(1.07, 1.07, 1.07)
      rotate3d(
        ${center.y / 100},
        ${-center.x / 100},
        0,
        ${Math.log(distance) * 2}deg
        )
        `;
        
        $card.querySelector('.glow').style.backgroundImage = `
        radial-gradient(
          circle at
          ${center.x * 2 + bounds.width / 2}px
          ${center.y * 2 + bounds.height / 2}px,
      #ffffff55,
      #0000000f
    )
    `;
  }

  $card.addEventListener('mouseenter', () => {
      bounds = $card.getBoundingClientRect();
      document.addEventListener('mousemove', rotateToMouse);
    });
    
    $card.addEventListener('mouseleave', () => {
      document.removeEventListener('mousemove', rotateToMouse);
      $card.style.transform = '';
      $card.style.background = '';
    });
  }, 10)

  
  setTimeout(() => {
    const $card = document.querySelector('.cardsu');
    let bounds;

    function rotateToMouse(e) {
      const mouseX = e.clientX;
      const mouseY = e.clientY;
      const leftX = mouseX - bounds.x;
      const topY = mouseY - bounds.y;
      const center = {
        x: leftX - bounds.width / 2,
        y: topY - bounds.height / 2
      }
      const distance = Math.sqrt(center.x ** 4 + center.y ** 4);

      $card.style.transform = `
    scale3d(1.07, 1.07, 1.07)
    rotate3d(
      ${center.y / 100},
      ${-center.x / 100},
      0,
      ${Math.log(distance) * 2}deg
      )
      `;
      
      $card.querySelector('.glowu').style.backgroundImage = `
    radial-gradient(
      circle at
      ${center.x * 2 + bounds.width / 2}px
      ${center.y * 2 + bounds.height / 2}px,
      #ffffff55,
      #0000000f
    )
  `;
    }

    bounds = $card.getBoundingClientRect();
    $card.addEventListener('mouseenter', () => {
      document.addEventListener('mousemove', rotateToMouse);
    });

    $card.addEventListener('mouseleave', () => {
      document.removeEventListener('mousemove', rotateToMouse);
      $card.style.transform = '';
      $card.style.background = '';
    });
  }, 12)

  

  AOS.init({ duration : 3000});
  return (
    <div className='toko--cont'>
      <h2 className='toko--heading' data-aos="zoom-up" data-aos-duration='1500'>Tokenomics</h2>
      <div className='toko' >
        <div className='toko--cards glow' data-aos="flip-right" data-aos-delay='200'>
          <img src="https://raw.githubusercontent.com/AGRIVERSEdevelopers/resources/New/Tokenomics-new1.jpg" alt="card1" />
          {/* <h2>Card1</h2> */}
        </div>
        <div className='toko--cards cardsu glowu' data-aos="flip-left" data-aos-delay='300'>
          <img src="https://raw.githubusercontent.com/AGRIVERSEdevelopers/resources/New/T1.jpg" alt="card1" />
        </div>
      </div>
    </div>
  )
}


