import Navbar from "./components/Navbar";
import Main from "./components/Main";
import Footer from "./components/Footer";
import Roadmap from "./components/Roadmap";
import Populatediv from "./components/Populatediv";
import About from "./components/About";
import Cards from "./components/Cards";
import Login from "./components/Login";
import TermCards from "./components/TermCards";
import Cardsection from "./components/Cardsection";
import { Routes, Route } from "react-router-dom";
import MyProvider from "./components/MyProvider";
import ErrorBoundary from "./components/ErrorBoundary";
import AboutLink from "./components/AboutLink";
import Nft from './components/Nft'
import "./App.css";
import Faq from "./components/Faq";
import Vision from "./components/Vision";
import Tokonomics from "./components/Tokonomics";
import Comingsoon from './components/Comingsoon'
import VideoSlider from './components/VideoSlider';

function App() {
  
  return (
    <ErrorBoundary>
      <MyProvider>
        <Routes>
          <Route
            exact
            path="/agriverse/"
            element={
              <>
                <Navbar />
                <Main />
                <AboutLink />
                <TermCards />
                <Nft />
                <Cards />
                <VideoSlider />
                <Tokonomics />
                <Roadmap/>
                <Populatediv/>
                <Faq />
                <Vision />
                <Footer />
              </>
            }
          />
          <Route exact path="/agriverse/Roadmap" element={<>
            <Navbar />
            <Roadmap />
            <Populatediv />
          </>
          } />
          <Route exact path="/agriverse/About" element={<>
            <Navbar />
            <About />
          </>
          }
          />
          <Route
            exact
            path="/agriverse/Cardsection/*"
            element={
              <>
                <Navbar />
                <Cardsection />
              </>
            }
          ></Route>
          <Route
            exact
            path="/agriverse/Comingsoon"
            element={
              <>
                <Navbar />
                <Comingsoon />
              </>
            }
          ></Route>
          <Route
            exact
            path="/agriverse/login"
            element={
              <>
                <Login />
              </>
            }
          ></Route>
        </Routes>
      </MyProvider>
    </ErrorBoundary>
  );
}
export default App;
