import React from "react";
import "./css/login.css";

export default function Login() {
  return (
    <div className="login-body">
      <div id="login1">
        <div id="form1">
          <h1>Login</h1>
          <div id="form-div1">
            <div>
              <i className="fa fa-envelope-o"></i>
              <input type="email" name="email" id="email" autoComplete="off" placeholder="Input your user ID or Email"
                required="required" />
            </div>
            <div>
              <i className="fas fa-key"></i>
              <input type="password" name="password" id="password" placeholder="Input your password"
                required="required" />
            </div>
            <div id="forgot-div1">
              <input type="checkbox" name="check" id="check"/>
              <label htmlFor="check">Remember me</label>
              <a href="#login1">Forgot Password?</a>
            </div>
            <button className="wrap" type="submit"><a href="/agriverse"><i className='fas fa-sign-in-alt'></i> LOG IN</a></button>
          </div>
        </div>
        <div id="robo-hand">
          <h3>WELCOME!</h3>
          <p>Enter your details and start journey with us</p>
          <button ><a href="/agriverse">SIGNUP</a></button>
        </div>
      </div>
    </div>
  );
}
